.info_main {
  width: 100%;
  overflow: hidden;
  .inner {
    width: 100%;
    height: 100vh;
    display: flex;
    .info_description {
      display: flex;
      align-items: center;
      justify-content: center;
      flex: 1;
      > div {
        padding: 100px;

        .info_list {
          display: block;
          margin-bottom: 30px;
          padding-bottom: 10px;
          display: flex;
          justify-content: space-between;
          border-bottom: 1px solid rgb(226, 226, 226);
          span {
            font-family: 'Raleway', sans-serif;
            display: block;
            position: relative;
            color: rgb(180, 180, 180);
            cursor: pointer;
            &::after {
              content: '';
              display: block;
              width: 100%;
              height: 1px;
              position: absolute;
              bottom: -11px;
            }
            &.on {
              color: rgb(56, 83, 75);
              font-weight: bold;
              &::after {
                background-color: rgb(56, 83, 75);
              }
            }
          }
        }
        h1 {
          font-size: 60px;
          font-family: 'Raleway', sans-serif;
          margin-bottom: 30px;
          span {
            display: block;
          }
        }
        p {
          font-size: 18px;
          font-weight: bold;
          line-height: 25px;
          font-family: 'Raleway', sans-serif;
          margin-bottom: 40px;
          span {
            display: block;
          }
        }
      }
    }
    .info_image {
      flex: 1;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}

@media screen and (max-width: $tablet) {
  .info_main {
    .inner {
      height: 90%;
      flex-direction: column;
      .info_description {
        > div {
          box-shadow: 0px 1px 7px 0px rgba(0, 0, 0, 0.31);
          z-index: 3;
          padding: 80px 0px;

          .info_list {
            justify-content: space-around;
          }
          h1 {
            font-size: 50px;
            text-align: center;
            span {
              display: inline-block;
            }
          }
          p {
            padding: 0 100px;
            text-align: center;
            margin-bottom: 0;
            span {
              display: inline;
            }
          }
          button {
            display: none;
          }
        }
      }
    }
  }
}
@media screen and (max-width: $mobile) {
  .info_main {
    .inner {
      height: 90%;
      flex-direction: column;
      .info_description {
        > div {
          box-shadow: 0px 1px 7px 0px rgba(0, 0, 0, 0.31);
          z-index: 3;
          padding: 20px 0px;

          .info_list {
            justify-content: space-around;
            margin-bottom: 20px;
            span {
              font-size: 13px;
            }
          }
          h1 {
            display: none;
          }
          p {
            padding: 0 13px;
            text-align: center;
            margin-bottom: 0;
            line-height: 17px;
            span {
              display: inline;
              font-size: 12px;
            }
          }
          button {
            display: none;
          }
        }
      }
    }
  }
}
